import { NextIntlClientProvider, useMessages } from 'next-intl'
import { Geist, Geist_Mono } from 'next/font/google'
import Providers from '@/components/Providers'        // client wrapper
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import '@/globals.css'
import { use } from 'react'

const geistSans = Geist({ variable: '--font-geist-sans', subsets: ['latin'] })
const geistMono = Geist({ variable: '--font-geist-mono', subsets: ['latin'] })

export default async function LocaleLayout({
  children,
  params,
}: {
  children: React.ReactNode
  params: Promise<{ locale: string }>
}) {
  const {locale} = await params;
  const messages = useMessages()                // works only on server
  return (
    <html lang={locale}>
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
        {/* Providers is a client component that contains Redux + bootstrap */}
        <Providers locale={locale} messages={messages}>
          <Header />
          {children}
          <Footer />
        </Providers>
      </body>
    </html>
  )
}
